.performance .caching {
  border: 1px solid #D7DBE0;
  border-radius: 3px;
  background-color: #FFFFFF;
}

.performance .caching {
  position: relative;
  padding: 24px;
}
.performance .caching__title, .performance .caching__subtitle {
  margin: 0 !important;
}
.performance p {
  margin-top: 8px;
  margin-bottom: 24px;
  font-size: 16px;
}
.performance input[type=radio] {
  margin-top: 1px;
}
.performance .cache-level label {
  font-weight: 600;
}
.performance .cache-level .row {
  display: flex;
  margin-top: 17px;
}
.performance .cache-level .row:last-child {
  margin-bottom: 32px;
}
.performance .cache-level .row p {
  margin-bottom: 0;
}
.performance .onoffswitch {
  position: absolute;
  top: 24px;
  right: 24px;
}
@media screen and (max-width: 782px) {
  .performance input[type=radio] {
    height: 1rem;
    width: 1rem;
  }
  .performance input[type=radio]:checked:before {
    height: 0.5rem;
    width: 0.5rem;
    margin: 0.1875rem;
  }
}